import anUploadImg from '@/components/an-uploadImg/an-uploadImg.vue'
import anUploadVideo from '@/components/an-uploadVideo/an-uploadVideo.vue'
import anUploadAdv from '@/components/an-uploadAdv/an-uploadAdv.vue'
import uniIcon from '@/components/uni-icons/uni-icons.vue'
import banner from '@/components/banner/banner.vue'
import link from '@/components/link/link.vue'
import contact from '@/components/contact/contact.vue'
export default {
	components:{
	        anUploadImg,
			anUploadVideo,
			anUploadAdv,
			uniIcon,
			banner,
			contact
	},
	data() {
		return {
			title: 'Hello',
			par:{
				address:'',
				name:'',
				longitude:0,
				latitude:0
			},
			decode:true,
			img:[
				{
					type:'room'
				}
			],
			companyInformation:{
				innerImgs:[],
				realImgs:[],
				videos:[],
				info:'',
				realPhone:'',
				owner:'',
				address:'',
				addressType:'',
				addressDetail:'',
				addressName:'',
				longitude:0,
				latitude:0,
				advImg:[],
				advLikn:'',
				buyAdv:0,
				contack:'',
				explain:'',
				codeName:'',
				otherContacts:''
			},
			validatePhone:"",
			read:false,
			type:'create'
			
			
		}
	},
	
	onLoad(option) {	
		let _this = this;
		if(option.res){
			this.par = JSON.parse(option.res);
		}
		
		if(option.data){
			this.type = 'edit';
			const s = JSON.parse(decodeURIComponent(option.data));
			console.log(s);
			for(var key in s){
				_this.companyInformation[key] = s[key];
			}
			// companyInformation:{
			// 	innerImgs:[],
			// 	realImgs:[],
			// 	videos:[],
			// 	info:'',
			// 	realPhone:'',
			// 	owner:'',
			// 	address:'',
			// 	addressType:'',
			// 	addressDetail:'',
			// 	addressName:'',
			// 	longitude:0,
			// 	latitude:0,
			// 	advImg:[],
			// 	advLikn:'',
			// 	buyAdv:0,
			// 	contack:'',
			// 	explain:'',
			// 	codeName:'',
			// 	otherContacts:''
			// },
		}
		
	},
	onShow(){
		
		
	},
	methods: {
		xy(){
			uni.navigateTo({
				"url":"/pages/xy/xy"
			})
		},
		blade(){
			uni.showToast({
				"title":"此功能尚未开发，敬请期待"
			})
		},
		buyAdv17(){
			if(this.companyInformation.buyAdv){
				uni.showToast({
					"title":"关闭广告位成功"
				})
				this.companyInformation.buyAdv = 0;
				this.$refs.uploadAdv.refresh();
				this.companyInformation.advLikn = '';
			}else{
				uni.showToast({
					"title":"开启广告位成功，广告位将收取一定费用"
				})
				this.companyInformation.buyAdv = 1;
				
			}
			
		},
		refreshImageList(list,type){
			
			if(type == 'room'){
				this.companyInformation.innerImgs = list
			}
			if(type == 'live'){
				this.companyInformation.realImgs = list
			}
			if(type == 'activity'){
				this.companyInformation.advImgs = list;
				console.log(list);
				console.log(this.companyInformation.advImg);
			}
			
			
		},
		refreshVideoList(list){
			console.log(123)
			this.companyInformation.videos = list
		},
		
		payAll(){			
			let _this = this;
			if(this.read){
				uni.showLoading({
					"title":"正在生成二维码",
					"mask":"true"
				})
				
				uni.showLoading({
					"title":"正在生成"
				})
				console.log(_this.companyInformation);
				if(_this.companyInformation.addressDetail == ""){
					uni.showToast({
						"title":"详细地址不能为空",
						"icon":"none"
					})
					return false;
				}
				if(_this.companyInformation.addressName == ""){
					uni.showToast({
						"title":"地址名称不能为空",
						"icon":"none"
					})
					return false;
				}
				
				
				
					if(_this.companyInformation.buyAdv == 1){
						
						
						this.$ajax("/nav/apply","POST",_this.companyInformation).then(res=>{
							uni.hideLoading();
							if(res){
								uni.requestPayment({
									provider: "wxpay",
									timeStamp:res.payment.timeStamp,
									nonceStr:res.payment.nonceStr,
									package:res.payment.package,
									signType:res.payment.signType,
									paySign:res.payment.paySign,
									success:function(){
										uni.setStorageSync('qrcodeNo',res.no);
										uni.reLaunch({
											"url":"/pages/address/msdh/dhsc?qrcode="+res.no
										})
									},
									fail:function(){
										uni.showToast({
											"title":"取消支付",
											"icon":"none"
										})
									}
								})
							}else{
								uni.showToast({
									"title":"服务器错误，请稍后再试",
								})
							}
							
						})
					}else{
						
						this.$ajax("/nav/apply","POST",_this.companyInformation).then(res=>{
							uni.hideLoading();
							if(res){
								uni.setStorageSync('qrcodeNo',res.no);
								uni.navigateTo({
									"url":"/pages/address/msdh/dhsc?qrcode="+res.no
								})
							}else{
								uni.showToast({
									"title":"服务器异常,请稍后再试",
									"icon":"none"
								})
							}
							
							
						})
					}
				
				
				
				
				
	
			}else{
				uni.showToast({
					"title":"请先阅读免责协议，勾选后再提交",
					"icon":"none"
				})
			}
			
		},
		toSearch(){
			let _this = this;
			uni.chooseLocation({
				 keyword:"",
				 success: function (res) {
					_this.par = res;		         
				 }
			})
		},
		tip(){
			if(this.companyInformation.buyAdv == 0){
				uni.showToast({
					"title":"亲，请先按下方按钮开启广告位",
					"icon":"none"
				})
			}
		},
		
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		}
	},
	watch:{
		par: {
		    handler(newName, oldName) {
		      this.companyInformation.address = newName.name+"("+newName.address+")";
			  this.companyInformation.latitude = newName.latitude;
			  this.companyInformation.longitude = newName.longitude
		    },
			deep:true,
		    immediate: true
		}
	}
}
